import React from "react";
import { Modal, Button } from "react-bootstrap";
import { Box, Typography, Stack, Avatar, Divider, Grid, Paper } from "@mui/material";
import { genrateUrl } from "../../../utils/genrateUrl";

const ProfileModal = ({ show, handleClose, profiledata }) => {
  if (!profiledata) return null;

  const business = profiledata?.businessInfo || {};
  const bank = profiledata?.bankDetails || {};
  const userType = profiledata?.user_type_id?.user_type || "-";

  return (
    <Modal
      show={show}
      onHide={handleClose}
      centered
      size="md"
      className="custom-modal"
      backdrop="static"
    >
      <Modal.Header closeButton style={{ backgroundColor: "var(--bs-primary1)" }}>
        <Modal.Title style={{ color: "var(--bs-primary2)" }}>Profile Details</Modal.Title>
      </Modal.Header>

      <Modal.Body style={{ backgroundColor: "var(--bs-primary1)", maxHeight: "70vh", overflowY: "auto" }}>
        {/* Profile Header */}
        <Stack alignItems="center" spacing={1.5} mb={2}>
          <Avatar
            src={genrateUrl(profiledata?.profile)}
            sx={{
              width: 90,
              height: 90,
              border: "2px solid var(--bs-primary2)",
              boxShadow: "0 0 10px rgba(0,0,0,0.2)",
            }}
          />
          <Typography variant="h6" sx={{ fontWeight: 600, textAlign: "center", color: "var(--bs-primary2)" }}>
            {profiledata?.name || "—"}
          </Typography>
          <Typography variant="body2" sx={{ color: "var(--bs-primary2)", textAlign: "center" }}>
            Code: {profiledata?.refer_id || "N/A"}
          </Typography>
        </Stack>

        {/* Wallet Summary */}
        <Stack direction="row" spacing={1.5} justifyContent="center" mb={2} flexWrap="wrap">
          {[
            { label: "Main", value: profiledata?.main_wallet },
            { label: "AEPS", value: profiledata?.aeps_wallet },
            { label: "Cashback", value: profiledata?.cashback_wallet },
            { label: "Locking", value: profiledata?.locking_amt },
          ].map((wallet) => (
            <Paper
              key={wallet.label}
              sx={{
                p: 1.5,
                minWidth: 80,
                textAlign: "center",
                backgroundColor: "var(--bs-primary1)",
                border: "1px solid var(--bs-primary2)",
                mb: 1,
              }}
            >
              <Typography variant="body2" sx={{ color: "var(--bs-primary2)", fontWeight: 600 }}>
                {wallet.label}
              </Typography>
              <Typography variant="body2" sx={{ color: "var(--bs-primary2)" }}>
                ₹{wallet.value || 0}
              </Typography>
            </Paper>
          ))}
        </Stack>

        <Divider sx={{ mb: 2, borderColor: "var(--bs-primary2)" }} />

        {/* Personal Details */}
        <Paper
          elevation={2}
          sx={{
            p: 2,
            mb: 2,
            backgroundColor: "var(--bs-primary1)",
            border: "1px solid var(--bs-primary2)",
          }}
        >
          <Typography variant="subtitle1" sx={{ fontWeight: 600, color: "var(--bs-primary2)", mb: 1 }}>
            Personal Information
          </Typography>
          <Grid container spacing={1}>
            <Grid item xs={6}>
              <Typography variant="body2" sx={{ color: "var(--bs-primary2)" }}>
                <strong>Email:</strong> {profiledata?.email || "—"}
              </Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography variant="body2" sx={{ color: "var(--bs-primary2)" }}>
                <strong>Mobile:</strong> {profiledata?.mobile || "—"}
              </Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography variant="body2" sx={{ color: "var(--bs-primary2)" }}>
                <strong>State:</strong> {profiledata?.state || "—"}
              </Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography variant="body2" sx={{ color: "var(--bs-primary2)" }}>
                <strong>PIN:</strong> {profiledata?.pinCode || "—"}
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <Typography variant="body2" sx={{ color: "var(--bs-primary2)" }}>
                <strong>Address:</strong> {profiledata?.presentAddr || "—"}
              </Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography variant="body2" sx={{ color: "var(--bs-primary2)" }}>
                <strong>User Type:</strong> {userType}
              </Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography variant="body2" sx={{ color: "var(--bs-primary2)" }}>
                <strong>Education:</strong> {profiledata?.educationQualification || "—"}
              </Typography>
            </Grid>
          </Grid>
        </Paper>

        {/* Business Details */}
        <Paper
          elevation={2}
          sx={{
            p: 2,
            mb: 2,
            backgroundColor: "var(--bs-primary1)",
            border: "1px solid var(--bs-primary2)",
          }}
        >
          <Typography variant="subtitle1" sx={{ fontWeight: 600, color: "var(--bs-primary2)", mb: 1 }}>
            Business Information
          </Typography>
          {profiledata?.isGstAvailable ? (
            <>
              <Typography variant="body2" sx={{ color: "var(--bs-primary2)", mb: 1 }}>
                <strong>GST Number:</strong> {business?.gst_NUmber || "—"}
              </Typography>
              <Typography variant="body2" sx={{ color: "var(--bs-primary2)", mb: 1 }}>
                <strong>Legal Name:</strong> {business?.legal_name_of_business || "—"}
              </Typography>
              <Typography variant="body2" sx={{ color: "var(--bs-primary2)", mb: 1 }}>
                <strong>Business Type:</strong> {business?.constitution_of_business || "—"}
              </Typography>
            </>
          ) : (
            <>
              <Typography variant="body2" sx={{ color: "var(--bs-primary2)", mb: 1 }}>
                <strong>Shop Name:</strong> {profiledata?.shop_name || "—"}
              </Typography>
            </>
          )}
          <Typography variant="body2" sx={{ color: "var(--bs-primary2)" }}>
            <strong>Address:</strong> {business?.business_Address || profiledata?.business_Address || "—"}
          </Typography>
          <Typography variant="body2" sx={{ color: "var(--bs-primary2)" }}>
            <strong>Area:</strong> {business?.business_Area || profiledata?.business_Area || "—"}
          </Typography>
          <Typography variant="body2" sx={{ color: "var(--bs-primary2)" }}>
            <strong>City:</strong> {business?.business_city || profiledata?.business_city || "—"}
          </Typography>
        </Paper>

        {/* Bank Details */}
        {bank && (
          <Paper
            elevation={2}
            sx={{
              p: 2,
              mb: 2,
              backgroundColor: "var(--bs-primary1)",
              border: "1px solid var(--bs-primary2)",
            }}
          >
            <Typography variant="subtitle1" sx={{ fontWeight: 600, color: "var(--bs-primary2)", mb: 1 }}>
              Bank Details
            </Typography>
            <Typography variant="body2" sx={{ color: "var(--bs-primary2)", mb: 1 }}>
              <strong>Bank Name:</strong> {bank?.bank_name || "—"}
            </Typography>
            <Typography variant="body2" sx={{ color: "var(--bs-primary2)", mb: 1 }}>
              <strong>Account Number:</strong> {bank?.bank_account_number || "—"}
            </Typography>
            <Typography variant="body2" sx={{ color: "var(--bs-primary2)", mb: 1 }}>
              <strong>IFSC:</strong> {bank?.bank_ifsc || "—"}
            </Typography>
            <Typography variant="body2" sx={{ color: "var(--bs-primary2)" }}>
              <strong>Account Holder:</strong> {bank?.bank_account_name || "—"}
            </Typography>
          </Paper>
        )}
      </Modal.Body>

      <Modal.Footer style={{ backgroundColor: "var(--bs-primary1)" }}>
        <Button variant="secondary" onClick={handleClose}>
          Close
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default ProfileModal;
